.projects-info-card {
    min-height : 23rem ;
    border-radius: 0.60rem;
    border: 1px solid #6751b9;
    background: rgba(0, 0, 0, 0.2);   
}


.projects-info-content {
    padding : 2rem ;
    flex: 1 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
}

.project-info {
    border: 1px solid #6751b9;
    max-width : 20rem ;
    padding: 1rem;
    max-height: 290px;
}

.project-info p {
    font-size: 0.9rem;
    font-weight: 300;
}

.project-info img {
    max-width: 278px;
    }

    img.skills-icons {
        max-width: 1.5rem;
        margin: 1rem 0.8rem;
    
    }
    
    img.skills-icons:hover {
        transform: translateY(-0.5rem);
    }
    
    .skills-icons {
        display: flex;
        position: relative;
        top: -3rem;
      
    }

  .project-lien {
    font-size: 0.9rem;
    font-weight: 600;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    top: -15.7rem;
    left: -2.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 8rem;
    height: 3rem;
   border-radius: 0.55rem;
   border: #6751b9 1px solid ;
   background-color: #060417;
  }


  @media (max-width: 1025px) {
    .projects-info-content {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
  @media (max-width: 783px) {
    .projects-info-content {

        grid-template-columns: repeat(1, 1fr);
    }
  }
  
  @media (max-width: 600px) {
    .project-info {
      max-width: 100%;
    }
  }


  @media (max-width: 1319px) {

    .projects-info-content {

        grid-template-columns: repeat(2, 1fr);
    }
}